.default {
    display: inline-block;
    padding: 10px 20px;
    text-align: center;
    font-family: inherit;
    font-size: 1rem;
    border: 1px solid rgba(102, 102, 108, 0.77);
    border-radius: 10px;
    margin: 5px;
    letter-spacing: 0.05rem;
    background-color: #ddecf1;
    color: rgba(102, 102, 108, 0.77);
}

.error {
    display: inline-block;
    padding: 10px 20px;
    text-align: center;
    font-family: inherit;
    font-size: 1rem;
    border: 1px solid rgb(206, 52, 52);
    border-radius: 10px;
    margin: 5px;
    letter-spacing: 0.05rem;
    background-color: #f5c5c5;
    color: rgba(255, 255, 255, 0.77);
}